/*GENERAL CSS*/

body {
    font-family: "Open Sans", sans-serif;
    color: #272829;
}

a.MuiTypography-root.MuiLink-root.MuiLink-underlineHover.MuiTypography-colorPrimary {
    color: #149ddd;
}

a.MuiTypography-root.MuiLink-root.MuiLink-underlineHover.MuiTypography-colorPrimary:hover {
    color: #37b3ed;
    text-decoration: none;
}


h1.MuiTypography-root.MuiTypography-h1, 
h2.MuiTypography-root.MuiTypography-h2,
h3.MuiTypography-root.MuiTypography-h3,
h4.MuiTypography-root.MuiTypography-h4,
h5.MuiTypography-root.MuiTypography-h5,
h6.MuiTypography-root.MuiTypography-h6 {
    font-family: 'Raleway', sans-serif;
}